
import { Input, Col, Row, Button, Select, Form } from 'ant-design-vue'
import { dispatchReceiveMessageEvent } from '@/utils/dispatchReceiveMessageEvent'
import { Types } from '@/utils/dispatchReceiveMessageEvent'

export default {
  name: 'MessageTrigger',
  data() {
    return {
      types: [
        {
          value: Types.success,
          label: '成功',
        },
        {
          value: Types.error,
          label: '失败',
        },
        {
          value: Types.warn,
          label: '提醒',
        },
        {
          value: Types.info,
          label: '信息',
        },
      ],
      formItemLayout: {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 3, offset: 6 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 9 },
        },
      },
      form: this.$form.createForm(this, { name: 'form' }),
    }
  },
  // beforeCreate() {
  //   this.form = this.$form.createForm(this, { name: 'form' })
  // },
  methods: {
    sendMessage() {
      this.form.validateFields((err, values) => {
        if (!err) {
          dispatchReceiveMessageEvent(values)
        }
      })
    },
  },
  components: {
    [Input.TextArea.name]: Input.TextArea,
    [Select.name]: Select,
    [Select.Option.name]: Select.Option,
    [Button.name]: Button,
    [Col.name]: Col,
    [Row.name]: Row,
    [Form.name]: Form,
    [Form.Item.name]: Form.Item,
  },
}
